	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import jianchaxiangmu from '@/views/jianchaxiangmu/list'
	import jianchabaogao from '@/views/jianchabaogao/list'
	import jianchayuyue from '@/views/jianchayuyue/list'
	import yonghu from '@/views/yonghu/list'
	import zaixianjiaoliu from '@/views/zaixianjiaoliu/list'
	import storeup from '@/views/storeup/list'
	import yisheng from '@/views/yisheng/list'
	import jianchaleixing from '@/views/jianchaleixing/list'
	import jiankangjihua from '@/views/jiankangjihua/list'
	import config from '@/views/config/list'
	import jiankangdaka from '@/views/jiankangdaka/list'
	import yishengRegister from '@/views/yisheng/register'
	import yishengCenter from '@/views/yisheng/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/yishengCenter',
			name: '医生个人中心',
			component: yishengCenter
		}
		,{
			path: '/news',
			name: '公告信息',
			component: news
		}
		,{
			path: '/jianchaxiangmu',
			name: '检查项目',
			component: jianchaxiangmu
		}
		,{
			path: '/jianchabaogao',
			name: '检查报告',
			component: jianchabaogao
		}
		,{
			path: '/jianchayuyue',
			name: '检查预约',
			component: jianchayuyue
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/zaixianjiaoliu',
			name: '在线交流',
			component: zaixianjiaoliu
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/yisheng',
			name: '医生',
			component: yisheng
		}
		,{
			path: '/jianchaleixing',
			name: '检查类型',
			component: jianchaleixing
		}
		,{
			path: '/jiankangjihua',
			name: '健康计划',
			component: jiankangjihua
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/jiankangdaka',
			name: '健康打卡',
			component: jiankangdaka
		}
		]
	},
	{
		path: '/yishengRegister',
		name: '医生注册',
		component: yishengRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
